body {
    width: 100%;
    padding: 0;
    margin: 0;
}

div {
    display: inline-block;
    flex-direction: row;
    height: 500px;
    width: 400px;
    border: 1px solid black;
    border-radius: 10px;
    background-color: rgb(124, 95, 95);
    margin-left: 20px;
    margin-bottom: 20px;
}

button {
    text-align: center;
}

img {}

p {
    text-align: center;
}

